html {}
body {
  font-family: 'Montserrat', sans-serif;
  font-size: 18px;
  box-sizing: border-box;
  position: relative; }
.container {
  width: 100% !important;
  max-width: 1160px;
  box-sizing: border-box;
  @include r(1200) {
    padding: 0 15px; } }
@font-face {
  font-family: 'Ailerons';
  src: url('../fonts/Ailerons-Regular.woff2') format('woff2'), url('../fonts/Ailerons-Regular.woff') format('woff');
  font-weight: normal;
  font-style: normal; }
a {
  text-decoration: none;
  transition: all 0.3s; }
input[type="submit"] {
  appearance: none;
  -webkit-appearance: none; }
textarea {
  resize: none; }


@include r(767) {
	.hidden-xs {
		display: none !important; } }
@media only screen and (min-width: 768px) and (max-width: 1024px) {
	.hidden-sm {
		display: none !important; } }
@include rmin(1024) {
	.hidden-md {
		display: none !important; } }


.btn {
  padding: 15px 40px;
  display: inline-flex;
  align-items: center;
  border-radius: 28px;
  font-size: 12px;
  font-weight: bold;
  line-height: 1.25;
  i {
    font-size: 20px;
    margin-right: 25px; }
  &-primary {
    border: solid 2px #131313;
    color: #131313; }
  &-secondary {
    background-color: #fdfdfd;
    color: #131313;
    letter-spacing: 1.2px;
    text-transform: uppercase; } }
