.media {
  padding: 80px 0;
  background-image: linear-gradient(135deg, #ffffff, #f8f8f8);
  &__content {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(365px, auto));
    grid-gap: 30px;
    grid-auto-rows: minmax(367px, auto);
    grid-auto-flow: dense; }
  &__block {
    overflow: hidden;
    @include center;
    img {
      max-width: 100%;
      @include object-fit(cover);
      width: 100%;
      height: 100%;
      @include tr; }
    &-video {
      position: relative;
      a {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        &:hover {
          transform: translate(-50%, -50%) scale(1.1); }
        svg {
          width: 133px; } } } }
  .span-1 {
    grid-column-end: span 2;
    grid-row-end: span 1; }
  .span-2 {
    grid-column-end: span 1;
    grid-row-end: span 2; }
  &__btn {
    @include center;
    margin-top: 75px;
    a {
      text-transform: uppercase;
      &:first-child {
        margin-right: 38px;
        i {
          font-size: 14px; } } } } }
